import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
// import { getSearch } from '../request/request';

const AppBar = () => {
	const [ input, setInput ] = useState('');
	let history = useHistory();

	function handleSearch(e) {
		e.preventDefault();
		setInput('');
		history.push(`/search/${input}`);
	}

	return (
		<div className="ui container small secondary menu pt-10">
			<Link className="item active" to="/">
				H
			</Link>
			<div className="item" id="search-bar">
				<form action="/search/" method="GET" className="ui icon input" onSubmit={(e) => handleSearch(e)}>
					<input
						type="text"
						name="q"
						placeholder="Search anime titles..."
						value={input}
						onChange={(e) => setInput(e.target.value)}
					/>
					<i className="search link icon" />
				</form>
			</div>
		</div>
	);
};

export default AppBar;
